    background: #424244;
    border-style:none;
    box-shadow: grey 2px 2px 2px;
    color: #ECECEC;
}

/* navbar main text */
.navbar-default .navbar-brand {
    color: #ECECEC;
    font-family: 'Pacifico', cursive;
    font-size: 28px;
    color: white;
}

