.wrapper {
  padding-top: 50px;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  text-align:center;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

div.row {
  margin:auto;
}

.profile-sm {
  height: 250px;
  overflow: hidden;
  text-align: center;
  box-shadow: rgb(59, 59, 59) 3px 3px 10px;
}

.f-card {
  margin:0px 40px 10px 40px;
  display:inline-block;
}

.f-text {
  font-weight:100;
  color:#ECECEC;
  padding:15px;
  font-size:18px;
}

#upload-link {
  margin: 0px auto 20px auto;
}

.label-default {
  background:#ECECEC;
  display: inline;
  padding: .2em .6em .3em;
  font-weight:100;
  color: #424244;
  text-align: center;
  vertical-align: baseline;
  border-radius: .25em;
}

.label-success {
  background:#00DA51;
  display: inline;
  padding: .2em .6em .3em;
  font-weight:100;
  color: #ECECEC;
  text-align: center;
  vertical-align: baseline;
  border-radius: .25em;
}
.img-card {
    display:inline-block;
    margin:0px 40px 69px 40px;
    height: 250px;
    overflow: hidden;
    text-align: center;
}

.button-refresh {
    border: none;
    padding: 0;
    text-decoration: none;
    background:none;
}
h2 {
    color: sienna;
}

.timer-buttons {
    padding: 5px 15px 5px 15px;
    margin: 20px 5px 20px 5px;
    border-radius: 6px;
}
.p-card {
  margin:0px 0px 20px auto;
}

.p-text {
  margin:auto;
  text-align:left;
  font-weight:100;
  color:black;
  padding:15px;
  font-size:14px;
  background:#ECECEC;
  box-shadow: grey 2px 2px 2px;
  border-radius:6px;
  max-width: 400px;
}

@media (min-width: 768px) {
  .col-sm-12 {
    margin:auto;
  }
}

hr {
  border-top: 1px solid lightgrey;
  margin: 0px auto 10px auto;
}
.p-input {
  margin:auto;
  text-align:right;
  font-weight:100;
  color:black;
  padding:15px;
  font-size:14px;
  background:#ECECEC;
  box-shadow: grey 2px 2px 2px;
  border-radius:6px;
  max-width: 400px;
}
body,
p {
  padding: 0;
  margin: 0;
}

body {
  background: #272726;
}

#remote-media {
  height: 43%;
  width: 100%;
  background-color: #fff;
  text-align: center;
  margin: auto;
}

#remote-media video {
  border: 1px solid #272726;
  margin: 3em 2em;
  max-width: 27% !important;
  background-color: #272726;
  background-repeat: no-repeat;
}

#controls {
  padding: 3em;
  max-width: 1200px;
  margin: 0 auto;
}

#controls {
  float: left;
}

#controls #room-controls,
#controls #preview,
#controls #identity-controls {
  width: 16em;
  margin: 0 1.5em;
  text-align: center;
}

#controls p.instructions {
  text-align: left;
  margin-bottom: 1em;
  font-family: Helvetica-LightOblique, Helvetica, sans-serif;
  font-style: oblique;
  font-size: 1.25em;
  color: #777776;
}

#controls button {
  width: 15em;
  height: 2.5em;
  margin-top: 1.75em;
  border-radius: 1em;
  font-family: 'Helvetica Light', Helvetica, sans-serif;
  font-size: 0.8em;
  font-weight: lighter;
  outline: 0;
}

#controls #room-controls input,
#controls #identity-controls input {
  font-family: Helvetica-LightOblique, Helvetica, sans-serif;
  font-style: oblique;
  font-size: 1em;
}

#controls button:active {
  position: relative;
  top: 1px;
}

#controls #preview #local-media {
  width: 270px;
  height: 202px;
  border: 1px solid #cececc;
  box-sizing: border-box;
  background-image: url(data:image/svg+xml;base64,);
  background-position: center;
  background-repeat: no-repeat;
  margin: 0 auto;
}

#controls #preview #local-media video {
  max-width: 100%;
  max-height: 100%;
  border: none;
}

#controls #preview #button-preview {
  background: url(data:image/svg+xml;base64,)1em
    center no-repeat #fff;
  border: none;
  padding-left: 1.5em;
}

#controls #log {
  border: 1px solid #686865;
}

#controls #room-controls {
  display: none;
}

#controls #room-controls input,
#controls #identity-controls input {
  width: 100%;
  height: 2.5em;
  padding: 0.5em;
  display: block;
}

#controls #room-controls button,
#controls #identity-controls button {
  color: #fff;
  background: 0 0;
  border: 1px solid #686865;
}

#controls #room-controls button#button-leave,
#controls #room-controls button#button-share-screen,
#controls #room-controls button#button-unshare-screen {
  display: none;
}

#controls #log {
  width: 35%;
  height: 9.5em;
  margin-top: 2.75em;
  text-align: left;
  padding: 1.5em;
  float: right;
  overflow-y: scroll;
}

#controls #log p {
  color: #686865;
  font-family: 'Share Tech Mono', 'Courier New', Courier, fixed-width;
  font-size: 1.25em;
  line-height: 1.25em;
  margin-left: 1em;
  text-indent: -1.25em;
  width: 90%;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  background:linear-gradient(#FF8D74,#FF5631);
  background-attachment: fixed;
  padding: 0;
  margin: 0;
  height: 100%;
}

.landing-logo {
  margin:auto;
  padding-top:0px;
  display:block;
  width:400px;
}

.text-block {
  margin:auto;
  text-align: justify;
  text-indent: 30px;
  font-size:16px;
  color:#ECECEC;
  width:500px;
  max-width:80%;
  padding-bottom:20px;
}

.landing-page {
  text-align:center;
  margin:auto;
}

.login-page {
  padding-top:30px;
  text-align:center;
  margin:auto;
  width:500px;
  max-width:80%;
}

.justo-button {
  display:inline-block;
  font-size:16px;
  padding:5px 12px 5px 12px;
  margin:5px 5px 5px 5px;
  background:#52C9C3;
  text-shadow: grey 1px 1px 1px;
  color:#ECECEC;
  box-shadow: grey 2px 2px 2px;
  border-radius:5px;
  border:none;
}

